<template>
	<view class="login-cotainer">
		<uni-icons type="contact-filled" size="100" color="#afafaf"></uni-icons>
		<button type="primary" class="btn-login" open-type="getUserInfo" @getuserinfo="getUser">一键登录</button>
		<text class="tips-text">登录后查看</text>
	</view>
</template>

 <script>
	import {mapMutations} from 'vuex'
	export default {
		name:"my-login",
		data() {
			return {
				
			};
		},
		methods:{
			...mapMutations('m_user', ['updateUserInfo', 'updateToken']),
			
			getUser(e){
				console.log(e)
				if(e.detail.errMsg === 'getUserInfo:fail auth ') return uni.$showMsg('取消')
				this.updateUserInfo(e.detail.userInfo)
				this.getToken(e.detail)
			},
			async getToken(info){
				const [err,res] = await uni.login().catch((err=>err))
				console.log(res)
				if(err ||res.errMsg !== 'login:ok') return uni.$showMsg('失败')
				const query ={
					code:res.code,
					encryptedData: info.encryptedData,
					iv: info.iv,
					rawData: info.rawData,
					signature: info.signature
				}
				const {data:loginResult} = await uni.$http.post('/api/public/v1/users/wxlogin',query)
				if(loginResult.meta.status !==200) return uni.$showMsg('登录失败')
				this.updateToken(loginResult.message.token)
				this.navigateBack()
			},
		}
	}
 </script>

<style lang="scss">
.login-cotainer{
	height: 750rpx;
	background-color: #f8f8f8;
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	
	position: relative;
	overflow: hidden;
	&::after{
		content: ' ';
		display: block;
		width: 100%;
		height: 40px;
		background-color: white;
		
		position: absolute;
		bottom: 0;
		left: 0;
		border-radius: 100%;
		transform: translateY(50%);
	}
	.btn-login{
		width: 90%;
		border-radius: 100px;
		margin: 15px 0;
		background-color: #C00000;
	}
	.tips-text{
		color: gray;
		font-size: 12px ;
		
	}
}
</style>